import React, { useState } from 'react';
import { Form, Button, Input, Modal} from 'antd';

const FormItem = Form.Item;

const formLayout = {
    labelCol: { span: 7 },
    wrapperCol: { span: 13 },
  };

export default function UpdateForm(props){
    const {onCancel,onSubmit,updateModalVisible,handleUpdateModalVisible,values,handleUpdate}=props;

    const [form] = Form.useForm();

    const [formVals,setFormVals] = useState({
        ...props.values,
    });

    console.log(props.values);
    const handleNext = async () => {
        const fieldsValue = await form.validateFields();
        setFormVals({ ...formVals, ...fieldsValue});
        handleUpdate({ ...formVals, ...fieldsValue});
      };

      const renderContent = () => {
        return (
          <>
            <FormItem
              name="url"
              label="api"
              rules={[{ required: true, message: 'url' }]}
            >
              <Input placeholder="" />
            </FormItem>
            <FormItem
              name="name"
              label="api"
              rules={[{ required: true, message: '' }]}
            >
              <Input placeholder="" />
            </FormItem>
    
            {(<FormItem
              name="roles"
              label=""
              rules={[{ required: true, message: '' }]}
            >
              <Input placeholder="" />
            </FormItem>)}
            {(<FormItem
            name="remark"
            label=""
            rules={[{ required: true, message: '' }]}
            >
            <Input placeholder="" />
            </FormItem>)}
          </>
        );
      };
    
      const renderFooter = () => {
        return (
          <>
            <Button onClick={() => handleUpdateModalVisible(false, values)}></Button>
            <Button type="primary" onClick={() => handleNext()}>
              
            </Button>
          </>
        );
      };
    

    console.log("haha");

    return (
    <Modal
        width={640}
        bodyStyle={{ padding: '32px 40px 48px' }}
        destroyOnClose
        title=""
        visible={updateModalVisible}
        footer={renderFooter()}
        onCancel={() => handleUpdateModalVisible()}
      >
  
        <Form
          {...formLayout}
          form={form}
          initialValues={{
            name: formVals.name,
            url: formVals.url,
            roles: formVals.roles,
            remark: formVals.remark,
          }}
        >
          {renderContent()}
        </Form>
      </Modal>)
}